<template>
  <div class="business-details">
    <div class="tab-buttons">
      <el-button
        :type="activeData.index === 1 ? 'primary' : 'default'"
        @click="activeData.index = 1"
      >
        参数设置
      </el-button>
      <el-button
        :type="activeData.index === 2 ? 'primary' : 'default'"
        @click="activeData.index = 2"
      >
        套餐配置
      </el-button>
    </div>
    <component :is="activeData.component"></component>
  </div>
</template>

<script lang="ts" setup>
import { reactive, markRaw, defineAsyncComponent, watch } from "vue";

const ParameterSetting = defineAsyncComponent(
  () =>
    import(
      "@components/business-management/ospreyCloudCall/sub-customer/details/third-party-docking/components/parameter-setting.vue"
    )
);
const PackageConfiguration = defineAsyncComponent(
  () =>
    import(
      "@components/business-management/ospreyCloudCall/sub-customer/details/third-party-docking/components/package-configuration.vue"
    )
);

defineOptions({
  name: "businessManagement.ospreyCloudCall.business.detail",
});

const activeData = reactive({
  index: 1,
  component: markRaw(ParameterSetting),
});

const tabList = [
  {
    label: "参数设置",
    value: 1,
    componentName: markRaw(ParameterSetting),
  },
  {
    label: "套餐配置",
    value: 2,
    componentName: markRaw(PackageConfiguration),
  },
];

const tabClick = (value: number) => {
  activeData.index = value;
  activeData.component = tabList[value - 1].componentName;
};

watch(
  () => activeData.index,
  (newValue) => {
    activeData.component = tabList[newValue - 1].componentName;
  }
);
</script>

<style lang="scss" scoped>
.tab-buttons {
  margin-bottom: 20px;

  .el-button {
    margin-right: 8px;
    height: 35px;
    border-radius: 20px;
  }
}
</style>
